.login_container {
	width: 100%;
	min-height: 100vh;
	background-color: #f5f5f5;
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 20px; /* Add padding for smaller screens */
    box-sizing: border-box;
}
.login_form_container {
	width: 900px;
	max-width: 100%; /* Ensure it doesn't exceed the screen width */
	height: 500px;
	display: flex;
	flex-direction: column; /* Default to column layout for smaller screens */
	border-radius: 10px;
	box-shadow: 0px 3px 3px -2px rgb(0 0 0 / 20%),
		0px 3px 4px 0px rgb(0 0 0 / 14%), 0px 1px 8px 0px rgb(0 0 0 / 12%);
}

.left {
	flex: 2;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	background-color: white;
	border-radius: 10px 10px 0 0; /* Adjust for smaller screens */
	padding: 20px;
}

.form_container {
	display: flex;
	flex-direction: column;
	align-items: center;
	width: 100%;
}

.form_container h1 {
	font-size: 32px;
	margin-top: 0;
}

.input {
	outline: none;
	border: none;
	width: 100%; /* Full width for smaller screens */
	max-width: 370px; /* Max width to keep it manageable */
	padding: 15px;
	border-radius: 10px;
	background-color: #edf5f3;
	margin: 5px 0;
	font-size: 14px;
}

.error_msg {
	width: 100%; /* Full width for smaller screens */
	max-width: 370px; /* Max width to keep it manageable */
	padding: 15px;
	margin: 5px 0;
	font-size: 14px;
	background-color: #f34646;
	color: white;
	border-radius: 5px;
	text-align: center;
}

.right {
	flex: 1;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	background-color: #3bb19b;
	border-radius: 0 0 10px 10px; /* Adjust for smaller screens */
	padding: 20px;
}

.right h1 {
	margin-top: 0;
	color: white;
	font-size: 32px;
	align-self: center;
}

.white_btn,
.green_btn {
	border: none;
	outline: none;
	padding: 12px 0;
	background-color: white;
	border-radius: 20px;
	width: 180px;
	font-weight: bold;
	font-size: 14px;
	cursor: pointer;
	margin: 10px 0; /* Add margin for spacing */
}

.green_btn {
	background-color: #3bb19b;
	color: white;
}

/* Media Queries */
@media (min-width: 768px) {
	.login_form_container {
		flex-direction: row; /* Row layout for larger screens */
	}

	.left {
		border-radius: 10px 0 0 10px; /* Restore original border radius */
	}

	.right {
		border-radius: 0 10px 10px 0; /* Restore original border radius */
	}
}

@media (max-width: 767px) {
	.form_container h1,
	.right h1 {
		font-size: 24px; /* Smaller font size for smaller screens */
	}

	.input,
	.error_msg {
		width: calc(100% - 30px); /* Adjust width for padding */
	}
}
